import ComInputCom from '@/components/ComInputCom.vue';
export default {
	data() {
		return {
			isPassword: true,
			isPassword1: true,
			newPwd: '',
			newPwd1: '',
			iscode: '',
			phone: '',
			passShow: false
		};
	},
	components: {
		ComInputCom
	},
	computed:{
		phoneClearBtnShow(){
			return !!this.newPwd
		}
	},
	onLoad(options) {
		this.iscode = options.code;
		this.phone = options.phone;
	},
	onBackPress(option) {
		plus.key.hideSoftKeybord();
	},
	
	methods: {
	
		newpassword: function(e) {
			this.newPwd = e.detail.value;
		},
		newpassword1: function(e) {
			this.newPwd1 = e.detail.value;
		},
		next() {
			var _this = this;
			if (this.newPwd == '') {
				uni.showToast({
					title: '请输入新密码',
					icon: 'none',
					duration: 2000
				});
				return false;
			}
			var str = /^[a-z0-9]{6,16}$/;
			if (!str.test(this.newPwd)) {
				uni.showToast({
					title: '密码为6-16位字母或数字',
					icon: 'none',
					duration: 2000
				});
				return false;
			}
			uni.request({
				url: this.url + 'users/forgot/change/',
				method: 'POST',
				data: {
					mobile: this.phone,
					password: this.newPwd,
					sec_password: this.newPwd,
					code: this.iscode
				},
				header: {},
				success(res) {
					if(res.statusCode==200){
						uni.redirectTo({
							url: '../login/login'
						});
						uni.showToast({
							title: '密码找回成功',
							icon: 'none',
							duration: 2000
						});
					}
					
				}
			});
		},
		phoneInputFocus() {
		},
		phoneInputBlur() {

		},
		// 隐藏显示密码
		clearPhoneVal() {
			this.passShow = !this.passShow;
		}
	}
};
